import { BrowserRouter, Routes, Route } from "react-router-dom";
import { ConfigProvider } from "antd";
import locale from "antd/es/locale/en_US";
import "./App.css";

import { PrivateRoutes, PublicRoutes } from "./routers";
import { Login } from "./pages/login/Login";
import { UserList, UserDetail } from "./pages/user";
import { JobinList, JobinDetail, JobinAdd } from "./pages/jobin";

import { Test01 } from "./pages/zombi";

function App() {
  return (
    <ConfigProvider locale={locale}>
      <BrowserRouter>
        <Routes>
          <Route element={<PrivateRoutes />}>
            <Route path="/home" element={<>home</>} />

            <Route path="/jobin" element={<JobinList />} />
            <Route path={`/jobin/:id`} element={<JobinDetail />} />
            <Route path={`/jobin/add`} element={<JobinAdd />} />

            <Route path="/user" element={<UserList />} />
            <Route path={`/user/:id`} element={<UserDetail />} />

            <Route path={`/test01`} element={<Test01 />} />
          </Route>

          <Route element={<PublicRoutes />}>
            <Route path="/login" element={<Login />} />
            <Route path="/" element={<Login />} />
            <Route path="*" element={<>404</>} />
          </Route>
        </Routes>
      </BrowserRouter>
    </ConfigProvider>
  );
}

export default App;
