import React, { useEffect, useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';

import { Form, Input, Select, Button, Cascader, Card, Space, Spin } from 'antd';
import { CONST_LOCAL, CONST_JOB_CATEGORY, CONST_JOB_SALARY } from '../../etc/Const'

export const JobinAdd: React.FunctionComponent = () => {

  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);

  useEffect(() => { }, []);

  const onFinish = (values: any) => {
    console.log(values);
    setLoading(true);
    const url = 'http://localhost:8080' + `/api/gguin/jobin/adm/add`;
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${localStorage.getItem("token")}`
      },
      body: JSON.stringify({
        title: values.title,
        company: values.company,
        companyEn: values.companyEn,
        category: values.category,
        desc: values.desc,
        local1: values.local[0],
        local2: values.local[1],
        price: values.price,
      }),
    };
    fetch(url, options)
      .then(response => response.json())
      .then(response => {
        console.log(url, response);
        setLoading(false);
        navigate(`/jobin/${response.id}`);
      });
  };


  return (
    <>
      <Spin size="large" spinning={loading} >
        <Space direction='vertical' size='small' style={{ padding: 10, width: '100%' }}>

          <Card size='small'>  </Card>

          <Card>
            <Form
              labelCol={{ span: 2 }}
              onFinish={onFinish}
              layout="horizontal">

              <Form.Item label="" name="local" rules={[{ required: true, message: ' ' }]}>
                <Cascader options={CONST_LOCAL} />
              </Form.Item>

              <Form.Item label="" name="category" rules={[{ required: true, message: ' ' }]}>
                <Select>
                  {CONST_JOB_CATEGORY.map((cate, i) =>
                    <Select.Option key={i} value={cate.value}>{cate.label}</Select.Option>
                  )}
                </Select>
              </Form.Item>

              <Form.Item label="" name="price" rules={[{ required: true, message: ' ' }]}>
                <Select>
                  {CONST_JOB_SALARY.map((cate, i) =>
                    <Select.Option key={i} value={cate.value}>{cate.label}</Select.Option>
                  )}
                </Select>
              </Form.Item>

              <Form.Item label="" name="company">
                <Input showCount maxLength={20} />
              </Form.Item>

              <Form.Item label=" ()" name="companyEn">
                <Input showCount maxLength={20} />
              </Form.Item>

              <Form.Item label="" name="title" rules={[{ required: true, message: ' ' }]}>
                <Input showCount maxLength={30} />
              </Form.Item>

              <Form.Item label="" name="desc">
                <Input.TextArea
                  showCount
                  maxLength={1000}
                  style={{ height: 120, marginBottom: 24 }}
                />
              </Form.Item>

              <Form.Item {...{ wrapperCol: { offset: 2, span: 16 } }}>
                <Button type="primary" htmlType="submit"></Button>
              </Form.Item>

            </Form>
          </Card>
        </Space>
      </Spin>
    </>
  );

};
