import React, { useEffect, useState, useRef } from "react";
import { useParams, Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { Card, Space, Descriptions, Tabs } from "antd";

import { JobinModel } from "../../model/JobinModel";
import Item from "antd/es/list/Item";

export const JobinDetail: React.FunctionComponent = () => {
  const jobId = useParams<{ id: string }>().id;
  const [data, setData] = useState<JobinModel.JobinDataModel>();
  const [translate, setTranslate] = useState<Array<JobinModel.TranslateDataModel>>([]);

  useEffect(() => {
    getInfo();
    getTranslate();
  }, []);

  const getInfo = async () => {
    console.log("getInfo");
    const url = "http://localhost:8080" + `/api/gguin/jobin/adm/${jobId}`;
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    };
    fetch(url, options)
      .then((response) => response.json())
      .then((response) => {
        console.log(url, response);
        setData(response.result);
      });
  };

  const getTranslate = async () => {
    console.log("getTranslate");
    const url = "http://localhost:8080" + `/api/gguin/jobin/adm/translate/${jobId}`;
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    };
    fetch(url, options)
      .then((response) => response.json())
      .then((response) => {
        console.log(url, response);
        setTranslate(response.result);
      });
  };

  return (
    <>
      <Card>
        <Space direction="vertical" size="large" style={{ width: "100%" }}>
          <Descriptions title=" " column={2} size="small" labelStyle={{ width: "14%" }} contentStyle={{ width: "36%" }} bordered>
            {[
              { title: "", span: 1, desc: data?.id },
              { title: "", span: 1, desc: data?.created },
              { title: "", span: 1, desc: data?.geoProvinceCode },
              { title: "", span: 1, desc: data?.geoMunicipalityCode },
              { title: "", span: 1, desc: data?.company },
              { title: " ()", span: 1, desc: data?.companyEn },
              { title: "", span: 1, desc: data?.price },
            ].map((item, i) => (
              <Descriptions.Item key={i} label={item.title} span={item.span}>
                {item.desc}
              </Descriptions.Item>
            ))}
          </Descriptions>

          <Descriptions title=" " column={2} size="small" labelStyle={{ width: "14%" }} bordered>
            <Descriptions.Item label={""} span={2}>
              {data?.title}
            </Descriptions.Item>
            <Descriptions.Item label={""} span={2}>
              {data?.description.split("\n").map((i, key) => {
                return (
                  <div key={key}>
                    {i}
                    <br />
                  </div>
                );
              })}
            </Descriptions.Item>
          </Descriptions>

          <Tabs
            defaultActiveKey="1"
            tabPosition="top"
            style={{}}
            items={translate.map((item, i) => {
              return {
                key: `${i}`,
                label: item.language,
                children: (
                  <>
                    <Descriptions column={2} size="small" labelStyle={{ width: "14%" }} bordered>
                      <Descriptions.Item label={""} span={2}>
                        {item.title}
                      </Descriptions.Item>
                      <Descriptions.Item label={""} span={2}>
                        {item.description}
                      </Descriptions.Item>
                    </Descriptions>
                  </>
                ),
              };
            })}
          />
        </Space>
      </Card>
    </>
  );
};
