import React, { useEffect, useState, useRef } from 'react';
import { Link } from 'react-router-dom';

import { Table, Card, Space, Input, Select, Button } from 'antd';
import { ReloadOutlined } from '@ant-design/icons';
import type { FormInstance } from 'antd/es/form';

import { UserModel } from '../../model/UserModel';

export const UserList: React.FunctionComponent = () => {

  const formRef = React.useRef<FormInstance>(null);
  const [data, setData] = useState<UserModel.Pagination<UserModel.UserDataModel>>();

  useEffect(() => {
    getList();
  }, []);

  const getList = async (page: number = 1) => {
    console.log('getList');
    const url = 'http://localhost:8080' + `/api/gguin/user/adm/list?page=${page}`;
    const options = {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${localStorage.getItem("token")}`
      }
    };
    fetch(url, options)
      .then(response => response.json())
      .then(response => {
        console.log(url, response.result);
        setData(response.result);
      });
  };

  const create = async () => {
    const url = 'http://localhost:8080' + `/api/gguin/account/adm/create`;
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${localStorage.getItem("token")}`
      },
      body: JSON.stringify({
        name: 'name',
        phone: '01012341234',
      }),
    };
    fetch(url, options)
      .then(response => response.json())
      .then(response => {
        console.log(url, response);
      });
  };

  const handleChange = (payload: any) => {
    console.log('payload', payload.current, payload);
    getList(payload.current);
  }

  return (
    <>
      <Space direction="vertical" size="middle" style={{ padding: 10, width: '100%' }}>

        <Space>
          {/* <Button onClick={e => create()}></Button> */}
          <Input.Search
            addonBefore={
              <Select defaultValue="user">
                <Select.Option value="user"></Select.Option>
                <Select.Option value="email"></Select.Option>
              </Select>
            } />
          <Button icon={<ReloadOutlined />} />
        </Space>

        <Table
          size='small'
          dataSource={data?.items ?? []}
          rowKey={(item) => item.id}
          onChange={handleChange}
          pagination={{
            current: data?.current,
            total: data?.total,
            pageSize: data?.pageSize,
            showSizeChanger: false,
            position: ['bottomCenter'],
          }}>
          <Table.Column
            title={'id'}
            align="center"
            render={(item: UserModel.UserDataModel) => (<>{item.id}</>)} />
          <Table.Column
            title={'name'}
            align="center"
            render={(item: UserModel.UserDataModel) => (<>{item.name}</>)} />
          <Table.Column
            title={'phone'}
            align="center"
            render={(item: UserModel.UserDataModel) => (<>{item.phone}</>)} />
        </Table>

      </Space>
    </>
  );

};
