import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";

import { Form, Button } from "antd";

interface ICustomFormCounterProps {
  value?: number;
  onChange?: (count: number) => void;
}

export const CustomFormCounter: React.FunctionComponent = (props: ICustomFormCounterProps) => {
  const [count, setCount] = useState(props.value || 0);

  const change = (n: number) => {
    const newCount = count + n;
    setCount(newCount);
    if (typeof props.onChange === "function") {
      props.onChange(newCount);
    }
  };

  return (
    <>
      <a onClick={() => change(-1)}>-</a>
      {count}
      <a onClick={() => change(1)}>+</a>
    </>
  );
};
