import React, { useEffect, useState, useRef } from 'react';
import { Link } from 'react-router-dom';

import { Table, Space, Input, Select, Button } from 'antd';
import { ReloadOutlined } from '@ant-design/icons';
import type { FormInstance } from 'antd/es/form';

import { JobinModel } from '../../model/JobinModel';

export const JobinList: React.FunctionComponent = () => {

  const formRef = React.useRef<FormInstance>(null);
  const [data, setData] = useState<JobinModel.Pagination<JobinModel.JobinDataModel>>();

  useEffect(() => {
    getList();
  }, []);

  const getList = async (page: number = 1) => {
    console.log('getList');
    const url = 'http://localhost:8080' + `/api/gguin/jobin/adm/list?page=${page}`;
    const options = {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${localStorage.getItem("token")}`
      }
    };
    fetch(url, options)
      .then(response => response.json())
      .then(response => {
        console.log(url, response.result);
        setData(response.result);
      });
  };

  const handleChange = (payload: any) => {
    console.log('payload', payload.current, payload);
    getList(payload.current);
  }

  return (
    <>
      <Space direction="vertical" size="middle" style={{ padding: 10, width: '100%' }}>

        <Space>
          {/* <Button onClick={e => { }}></Button> */}
          <Input.Search
            addonBefore={
              <Select defaultValue="user">
                <Select.Option value="user"></Select.Option>
                <Select.Option value="email"></Select.Option>
              </Select>
            } />
          <Button icon={<ReloadOutlined />} />
        </Space>

        <Table
          size='small'
          dataSource={data?.items ?? []}
          rowKey={(item) => item.id}
          onChange={handleChange}
          pagination={{
            current: data?.current,
            total: data?.total,
            pageSize: data?.pageSize,
            showSizeChanger: false,
            position: ['bottomCenter'],
          }}>
          <Table.Column
            title={'id'}
            align="center"
            render={(item) => (<Link to={`/jobin/${item.id}`}>{item.id}</Link>)} />
          <Table.Column
            title={''}
            align="center"
            render={(item) => (<>{item.geoProvinceCode} {item.geoMunicipalityCode}</>)} />
          <Table.Column
            title={''}
            align="center"
            render={(item) => (<>{item.category}</>)} />
          <Table.Column
            title={''}
            align="center"
            render={(item) => (<>{item.company}</>)} />
          <Table.Column
            title={''}
            align="center"
            render={(item) => (<>{item.price}</>)} />
          <Table.Column
            title={''}
            align="center"
            render={(item) => (<>{item.created}</>)} />
          <Table.Column
            title={''}
            align="center"
            render={(item) => (<>{item.title}</>)} />
        </Table>

      </Space>
    </>
  );

};
